.hero {
    padding-bottom: 55px;
    &__top {
        padding-bottom: 110px;
    }

    &__img {
        height: 584px;
    }
}

@media screen and (max-width: 991px) {
    .hero {
        &__top {
            padding-bottom: 70px;
        }

        &__svg {
            &-link svg {
                width: 139.18px;
                height: 50px;
            }
        }

        &__img {
            height: 865px;
        }
    }
}

@media screen and (max-width: 639px) {
    .hero {
        padding-bottom: 40px;
        &__top {
            padding-bottom: 50px;
        }

        &__svg {
            &-link svg {
                width: 83.51px;
                height: 30px;
            }
        }

        &__img {
            height: 480px;
        }
    }
}